body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.head {
    width: 20%;
    align-self: center;
    text-align: right;
    font-weight: bold;
    font-size: x-large;
}

.SearchBar{
    width: 100%;
    text-align: start;
    padding-left: 10px;
    
}

.SearchBar input {
    background-color: '#DFDFDF';
    padding-top: 3px;
    padding-bottom: 5px;
}


.heading{
    font-size: larger;
    font-weight: bold;
}
.light {
    color: #DFDFDF;
}
.label {
    font-weight: 500;
}
.desc {
    display: inline
}
